<template>
  <div>
    <nav id="menu">
      <ul class="menu__list">
        <li :class="0==currentindex ? 'menu__item menu__item--current':'menu__item'">
          <a class="menu__link" @click="toHome()">Home</a>
        </li>
        <li :class="1==currentindex ? 'menu__item menu__item--current':'menu__item'">
          <a class="menu__link" @click="toBlog()">Blog</a>
        </li>
        <li :class="2==currentindex ? 'menu__item menu__item--current':'menu__item'">
          <a class="menu__link" @click="toStudio()">Studio</a>
        </li>
        <li :class="3==currentindex ? 'menu__item menu__item--current':'menu__item'">
          <a class="menu__link" @click="toDaily()">Daily</a>
        </li>
        <li :class="4==currentindex ? 'menu__item menu__item--current':'menu__item'">
          <a class="menu__link" @click="toAbout()">About</a>
        </li>
      </ul>
    </nav>
  </div>
</template>


<script>
export default {
  name: "iris",
  components: {},
  data() {
    return {
      currentindex: 0,
      ways: ["home", "blog", "studio", "daily", "about"],
      menu: [
        "sebastian",
        "prospero",
        "viola",
        "antonio",
        "miranda",
        "ariel",
        "ferdinand",
        "stephano",
        "iris",
        "juno",
        "valentine",
        "puck"
      ]
    };
  },
  created() {
    this.keepNavRender = this.keepNavRender.bind(this);
  },
  mounted() {
    let me = this;
    me.setMenuStyle();
  },
  watch: {
    $route(to, from) {
      this.keepNavRender();
    }
  },
  methods: {
    // var el=event.currentTarget;
    // el.classList.add("menu__item--current");
    // console.log(el)
    setMenuStyle: function() {
      let me = this;
      let menu = document.getElementById("menu");
      let current = _.random(me.menu.length - 1);
      menu.className = "menu menu--" + me.menu[current];
    },

    keepNavRender() {
      let me = this;
      console.log(me.$route.path);
      let path = me.$route.path;
      for (let i in this.ways) {
        if (path.indexOf(this.ways[i]) > -1) {
          console.log(i);
          this.currentindex = i;
        }
      }
    },

    toHome: function() {
      let me = this;
      me.currentindex = 0;
      me.$router.push({
        name: "home"
      });
    },
    toBlog: function() {
      let me = this;
      me.currentindex = 1;
      //sessionStorage.setItem("currentindex", 0); 是按字符型存储的，如果在HTML中使用 === 运算符与整形运算，那么就是false。
      me.$router.push({
        name: "blog"
      });
    },
    toStudio: function() {
      let me = this;
      me.currentindex = 2;
      me.$router.push({
        name: "studio"
      });
    },
    toDaily: function() {
      let me = this;
      me.currentindex = 3;
      me.$router.push({
        name: "daily"
      });
    },
    toAbout: function() {
      let me = this;
      me.currentindex = 4;
      me.$router.push({
        name: "about"
      });
    }
  }
};
</script>


